import axios from 'axios';

// 
const list = (params) => axios.get('/api/admin/list', { params });
// 
const update = (formData) => axios.put('/api/admin/', formData);
// 
const remove = (id, params) => axios.delete(`/api/admin/${id}`, { params });

export default {
    list,
    update,
    remove
}
