import axios from 'axios';

import { Loading, Notification } from 'element-ui';

// 
import router from '@/router';

let loading;
// baseURLapi${baseURL}/api/path
// axios.defaults.baseURL = 'http://localhost:3003/';
// 
axios.interceptors.request.use(function (config) {
	// 
	loading = Loading.service({ background: 'rgba(0, 0, 0, 0.8)' });
	// api,token
	if (config.url == '/api/admin/login' || config.url == '/api/admin/register') {
		return config;
	}
	// token
	let { token } = sessionStorage;
	// token,headerstoken
	config.headers.Authorization = `Bearer ${token}`;
	return config;
}, function (error) {
	// 
	return Promise.reject(error);
});

// 
axios.interceptors.response.use(function ({ status, data, response }) {
	//
	loading.close();
	switch (status) {
		case 200:
			return data;
			break;
		default:
			Notification.error({
				title: `${status}`,
				message: 'network',
			});
			break;
	}
}, function ({ response: { status, data, statusText } }) {
	switch (status) {
		case 401:
			// 
			let expiredTime = new Date(data.inner.expiredAt).toLocaleString();
			Notification.error({
				title: `${status}`,
				message: `token${expiredTime}`,
			});
			// 
			let { fullPath } = router.history.current;
			router.replace({ path: '/login', query: { redirect: fullPath } });
			break;
		case 404:
			Notification.error({
				title: `${status}`,
				message: 'api',
			});
			break;
		case 500:
			Notification.error({
				title: `${status}`,
				message: '',
			});
			break;
		default:
			Notification.error({
				title: `${status}`,
				message: statusText,
			});
			break;
	}
});
